  font-weight: 700;
  line-height: 1.28;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1), border-radius .25s cubic-bezier(.4, 0, .2, 1);
}

.button--hovered:hover, .button--hovered:focus {
